h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --body-bg-color: #f6f6f6;
  --body-bg-color-transition: .3s;
  --header-bg-color: #fff;
  --header-bg-color-transition: .3s;
  --current-link-color: #111;
  --categories-link-color: rgba(17, 17, 17, .6);
  --black-text-color: #000;
  --primary-text-color: #111;
  --primary-text-color-transition: .3s;
  --grey-text-color: #b4afaf;
  --violet-text-color: #4f2ee8;
  --yellow-color: #eac645;
  --scroll-bar-color: #e0e0e0;
  --modal-window-color: #fff;
  --bg-shoping-list: #f6f6f6;
  --txt-shoping-list: rgba(0, 0, 0, .6);
  --bottomtxt-modal-window: rgba(17, 17, 17, .5);
  --default-tranzition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  background: var(--body-bg-color);
  transition: background-color var(--body-bg-color-transition);
  font-family: DM Sans;
}

.list {
  list-style-type: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip-path: inset(50%);
  white-space: nowrap;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-display: swap;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 400;
  src: url("DMSans-Regular.461ab9f9.woff2") format("woff2");
}

@font-face {
  font-display: swap;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  src: url("DMSans-Medium.63db2800.woff2") format("woff2");
}

@font-face {
  font-display: swap;
  font-family: DM Sans;
  font-style: italic;
  font-weight: 400;
  src: url("DMSans-Italic.108082e4.woff2") format("woff2");
}

@font-face {
  font-display: swap;
  font-family: DM Sans;
  font-style: normal;
  font-weight: 700;
  src: url("DMSans-Bold.8954ee88.woff2") format("woff2");
}

.header {
  margin-bottom: 40px;
}

.container-header {
  border: 1.5px solid var(--primary-text-color);
  background-color: var(--header-bg-color);
  transition: background-color var(--header-bg-color-transition);
  max-width: 375px;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
  padding: 18px 20px;
  display: flex;
}

.header-logo {
