.menu-container {
  width: 375px;
  height: 100vh;
  z-index: 999;
  background: linear-gradient(#4f2ee8, #fff);
  flex-direction: column;
  margin: 0 auto;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 70px;
  left: 0;
  right: 0;
