}

.burger-text.shopping {
  padding-right: 44px;
  position: relative;
}

.burger-shopping-icon {
  position: absolute;
}

.container_switch {
  align-items: center;
  display: flex;
}

.container_switch:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  padding-left: 0;
}

.container_switch:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  padding-right: 0;
}

.menu-btn-wrapper {
  margin-right: 0;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.theme_checkbox {
  height: 0;
  width: 0;
  opacity: 0;
}

.slider_round {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 27px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider_round:before {
  content: "";
  cursor: pointer;
  height: 18px;
  width: 18px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.theme_checkbox:checked + .slider_round {
  background: linear-gradient(#4f2ee8, #686868);
}

.theme_checkbox:checked + .slider_round:before {
  transform: translateX(20px);
}

.menu-container.js-is-open {
  transform: translateX(0%);
}

.js-is-hide {
  display: none;
}

.js-is-show {
  display: block;
}

